<template>
  <div class="back">
    <v-app-bar app elevation="2" color="#333744" dark>
      <v-app-bar-nav-icon @click="goBack()">
        <v-icon>mdi-arrow-left</v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        <span class="pr-8">新闻详情</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="1000">
          <p class="text-center pt-8 headline">{{ news.title }}</p>
          <div class="dotted_line mt-3 mx-5"></div>
          <p class="text-center grey--text text--darken-2 body-2 mt-5">
            <span class="mr-3">发布者: {{ news.publisher }}</span>
            <span class="mx-3">发布时间：{{ news.time }}</span>
            <span class="ml-3">点击量: {{ news.click }}</span>
          </p>
          <div class="mx-8">
            <p v-html="news.context">{{ news.context }}</p>
          </div>
          <p class="text-left mx-12 pt-4" v-if="news.fileURL">
            <v-icon>mdi-download</v-icon
            ><span class="text-left">附件下载：</span>
            <a :href="news.fileURL" :download="filename">{{ filename }}</a>
          </p>
          <div class="dotted_line mt-3 mx-5"></div>
          <div class="ml-8 mt-2">
            <p>
              <span
                >上一条：<a @click="lastAndNext(last.id)" v-if="last">{{
                  last.title
                }}</a></span
              >
            </p>
            <p>
              <span
                >下一条：<a @click="lastAndNext(next.id)" v-if="next">{{
                  next.title
                }}</a></span
              >
            </p>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'New',
  props: ['id'],
  components: {
    AppFooter,
  },
  watch: {
    $router() {
      this.getNew()
    },
  },
  data() {
    return {
      news: {
        title: '',
        publisher: '',
        time: '',
        context: '',
        click: '',
        fileURL: '',
      },
      filename: '',
      last: {},
      next: {},
    }
  },
  methods: {
    goBack() {
      this.$router.push('/newsList')
    },
    async getNew() {
      let res = await this.$http.get('/api/home/new?id=' + this.id)
      if (res.data.code === 200) {
        this.news = res.data.data.news
        this.filename = res.data.data.filename
        if (this.news.context.length !== 0) {
          this.news.context = this.news.context.replace(
            /img/g,
            'img style = " max-width:100%; "'
          )
        }
      }
      let lastRes = await this.$http.get('/api/home/newlast?id=' + this.id)
      if (lastRes.data.code === 200) {
        this.last = lastRes.data.data
      }
      let nextRes = await this.$http.get('/api/home/newnext?id=' + this.id)
      if (nextRes.data.code === 200) {
        this.next = nextRes.data.data
      }
    },
    lastAndNext(id) {
      this.$router.push('/newsList/detail/' + id)
      this.$router.go(0)
    },
  },
  mounted() {
    this.getNew()
  },
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
.dotted_line {
  width: 95%;
  height: 0;
  border-bottom: #c6c6c6 1px dashed;
}
</style>
